import { Github, Link } from "react-bootstrap-icons"
import { Container } from "./styles"
import Text from "src/components/common/Text"
import { ReactComponent as Figma } from "src/assets/icons/figma.svg"

interface LinkCardProps {
  value: string
  label: string
  type: string
}

const getLinkIcon = (type: string) => {
  switch (type) {
    case "repo":
      return <Github />
    case "prototype":
      return <Figma />
    default:
      return <Link />
  }
}

const getLinkTypeLabel = (type: string) => {
  switch (type) {
    case "repo":
      return "Repositrio"
    case "prototype":
      return "Prottipo"
    default:
      return "Link"
  }
}

const LinkCard = ({ type, value, label }: LinkCardProps) => {
  return (
    <Container>
      <a href={value} target="_blank" rel="noreferrer">
        {getLinkIcon(type)}
        <Text bold family="secondary" color="secondary" align="center">
          {label}
        </Text>
        <Text size="small" color="secondGray" align="center">
          {getLinkTypeLabel(type)}
        </Text>
      </a>
    </Container>
  )
}

export default LinkCard
