import Section from "src/components/common/Section"
import Text from "src/components/common/Text"
import timelineText from "src/constants/text/timeline.json"
import TimelineTable from "./TimelineTable"
import Task from "./Task"
import { TasksContainer } from "./styles"

const TimelineSection = () => (
  <Section title={timelineText.title} sectionKey="timeline">
    {timelineText.description.map((text, index) => (
      <Text key={index} parse>
        {text}
      </Text>
    ))}
    <TimelineTable />
    <TasksContainer>
      {timelineText.tasks.map((task, index) => (
        <Task {...task} key={index} />
      ))}
    </TasksContainer>
  </Section>
)

export default TimelineSection
