import Text from "src/components/common/Text"
import timelineText from "src/constants/text/timeline.json"
import { TableContainer, Table } from "./styles"
import useScrollButtons from "src/hooks/useScrollButtons"

const months = [
  "fev",
  "mar",
  "abr",
  "mai",
  "jun",
  "jul",
  "ago",
  "set",
  "out",
  "nov",
  "dez",
]

const TimelineTable = () => {
  const { containerRef, scrollHandler, buttons } =
    useScrollButtons<HTMLDivElement>()

  return (
    <>
      {buttons}
      <TableContainer ref={containerRef} onScroll={scrollHandler}>
        <Table>
          <thead>
            <tr>
              <th></th>
              {months.map((month) => (
                <th key={month}>
                  <Text color="secondary" bold>
                    {month.toUpperCase()}
                  </Text>
                </th>
              ))}
            </tr>
          </thead>
          <tbody>
            {timelineText.timeline.map((task) => (
              <tr key={task.taskName}>
                <td>
                  <Text bold color="primary">
                    {task.taskName}
                  </Text>
                </td>
                {months.map((_, index) => (
                  <td
                    key={Math.random()}
                    className={`${taskIsActive(task, index) ? "fill" : ""}`}
                  />
                ))}
              </tr>
            ))}
          </tbody>
        </Table>
      </TableContainer>
    </>
  )
}

const taskIsActive = (
  task: { startMonth: string; endMonth: string },
  monthIndex: number
) => {
  const startIndex = months.indexOf(task.startMonth)
  const endIndex = months.indexOf(task.endMonth)

  return startIndex <= monthIndex && monthIndex <= endIndex
}

export default TimelineTable
