import { Envelope, Github, Linkedin } from "react-bootstrap-icons"

import ps from "src/assets/images/ps.png"
import nh from "src/assets/images/nh.jpg"
import dl from "src/assets/images/dl.jpg"
import pm from "src/assets/images/pm.jpeg"
import { Card, ImagesContainer, ContentContainer } from "./styles"
import Text from "src/components/common/Text"

interface AuthorCardProps {
  index: number
  name: string
  responsibility: string
  links: {
    email: string
    github: string
    linkedin: string
  }
}

const icons = {
  email: <Envelope />,
  github: <Github />,
  linkedin: <Linkedin />,
}

const images = [ps, dl, pm, nh]

const getLink = (
  links: { github: string; linkedin: string; email: string },
  key: string
) => {
  switch (key) {
    case "email":
      if (!links.email) {
        return ""
      }
      return `mailto:${links.email}`
    case "github":
      if (!links.github) {
        return ""
      }
      return `https://github.com/${links.github}`
    case "linkedin":
      if (!links.linkedin) {
        return ""
      }
      return `https://linkedin.com/in/${links.linkedin}`
    default:
      return ""
  }
}

const AuthorCard = ({
  name,
  links,
  responsibility,
  index,
}: AuthorCardProps) => (
  <Card>
    <ImagesContainer>
      <img src={images[index]} alt={name} />
      <div className="icons">
        {Object.entries(icons).map(([key, icon]) => {
          const link = getLink(links, key)

          if (!link) {
            return null
          }

          return (
            <a key={key} href={link} target="_blank" rel="noreferrer">
              {icon}
            </a>
          )
        })}
      </div>
    </ImagesContainer>
    <ContentContainer>
      <Text bold color="primary" family="secondary">
        {name}
      </Text>
      <Text size="tiny" color="secondary" bold>
        {responsibility}
      </Text>
    </ContentContainer>
  </Card>
)

export default AuthorCard
