import { RefObject } from "react"
import FONT_SIZES from "src/constants/styling/font-sizes"
import SPACES from "src/constants/styling/spaces"

export const getActiveSection = () => {
  const sectionOffsets: number[] = []

  document.querySelectorAll(".section").forEach((section) => {
    let offset = section.getBoundingClientRect().top
    offset += section.clientHeight
    offset += window.scrollY
    offset -= SPACES.great + FONT_SIZES.huge + 2 * SPACES.small

    sectionOffsets.push(offset)
  })

  return sectionOffsets.findIndex((offset) => window.scrollY <= offset)
}

export const scrollToActiveButton = (
  buttonRef: RefObject<HTMLButtonElement>,
  containerRef: RefObject<HTMLDivElement>
) => {
  const button = buttonRef.current
  const container = containerRef.current

  if (button && container) {
    let scroll = button.getBoundingClientRect().left
    scroll -= container.getBoundingClientRect().left

    containerRef.current.scrollLeft = scroll
  }
}
