import styled from "styled-components"

import COLORS from "src/constants/styling/colors"
import FONT_FAMILIES from "src/constants/styling/font-families"
import FONT_SIZES from "src/constants/styling/font-sizes"
import { isSubtitle } from "src/utils/text"

interface TextProps {
  bold?: boolean
  color?: keyof typeof COLORS
  family?: keyof typeof FONT_FAMILIES
  size?: keyof typeof FONT_SIZES
  parse?: boolean
  align?: "center"
}

const StyledText = styled.span<TextProps>`
  font-family: ${(props) => FONT_FAMILIES[props.family || "primary"]};
  font-size: ${(props) => FONT_SIZES[props.size || "normal"]}px;
  color: ${(props) => COLORS[props.color || "thirdGray"]};
  font-weight: ${(props) => (props.bold ? "bold" : "normal")};
  text-align: ${(props) => props.align || "left"};
`

const Text = ({ children, ...props }: React.PropsWithChildren<TextProps>) => {
  if (typeof children !== "string" || !props.parse) {
    return <StyledText {...props}>{children}</StyledText>
  } else {
    const subtitle = isSubtitle(children)
    return (
      <StyledText {...props}>
        {children.split("**").map((item, index) => (
          <StyledText
            bold={index % 2 === 1}
            key={item.slice(0, 5) + index}
            color={props.color || (subtitle ? "secondary" : "thirdGray")}
          >
            {item}
          </StyledText>
        ))}
      </StyledText>
    )
  }
}

export default Text
