import { SectionContainer } from "./styles"

interface SectionProps {
  children?: React.ReactNode
  title: string
  sectionKey?: string
}

const Section = ({ children, title, sectionKey }: SectionProps) => {
  return (
    <SectionContainer id={sectionKey} className="section">
      <h2>{title}</h2>
      {children}
    </SectionContainer>
  )
}

export default Section
