import Section from "src/components/common/Section"
import Text from "src/components/common/Text"
import authorsText from "src/constants/text/authors.json"
import AuthorCard from "./AuthorCard"
import { AuthorsContainer } from "./styles"
import useScrollButtons from "src/hooks/useScrollButtons"

const AuthorsSection = () => {
  const { containerRef, scrollHandler, buttons } =
    useScrollButtons<HTMLUListElement>()

  return (
    <Section title={authorsText.title} sectionKey="authors">
      {authorsText.description.map((piece, index) => (
        <Text parse key={piece.slice(0, 5) + index}>
          {piece}
        </Text>
      ))}
      {buttons}
      <AuthorsContainer ref={containerRef} onScroll={scrollHandler}>
        {authorsText.authors.map((author, index) => (
          <AuthorCard {...author} index={index} key={index} />
        ))}
      </AuthorsContainer>
    </Section>
  )
}

export default AuthorsSection
