import { CheckSquareFill, Square } from "react-bootstrap-icons"
import { Container, Content } from "./styles"
import Text from "src/components/common/Text"

interface TaskProps {
  title: string
  description: string
  completed?: boolean
}

const Task = ({ title, description, completed }: TaskProps) => {
  return (
    <Container completed={completed}>
      {completed ? <CheckSquareFill /> : <Square />}
      <Content completed={completed}>
        <Text
          bold
          family="secondary"
          size="almostNormal"
          color={completed ? "primary" : "secondary"}
        >
          {title}
        </Text>
        <Text parse color={completed ? "primary" : "thirdGray"}>
          {description}
        </Text>
      </Content>
    </Container>
  )
}

export default Task
