import { useEffect, useRef, useState } from "react"
import { Button, ButtonsContainer } from "./styles"
import { ChevronCompactLeft, ChevronCompactRight } from "react-bootstrap-icons"

function useScrollButtons<RefType extends HTMLElement>() {
  const [canScroll, setCanScroll] = useState({ left: false, right: false })
  const containerRef = useRef<RefType>(null)

  const scrollHandler: React.UIEventHandler<RefType> = () => {
    if (!containerRef.current) {
      return
    }

    const { scrollLeft, scrollWidth, clientWidth } = containerRef.current

    if (scrollLeft && !canScroll.left) {
      setCanScroll((currentData) => ({ ...currentData, left: true }))
    }

    if (!scrollLeft && canScroll.left) {
      setCanScroll((currentData) => ({ ...currentData, left: false }))
    }

    if (
      Math.abs(scrollLeft + clientWidth - scrollWidth) > 0.5 &&
      !canScroll.right
    ) {
      setCanScroll((currentData) => ({ ...currentData, right: true }))
    }

    if (
      Math.abs(scrollLeft + clientWidth - scrollWidth) < 0.5 &&
      canScroll.right
    ) {
      setCanScroll((currentData) => ({ ...currentData, right: false }))
    }
  }

  const scrollButtonClickHandler = (right: boolean) => {
    if (!containerRef.current) {
      return
    }

    const container = containerRef.current

    if (right) {
      container.scrollLeft = Math.min(
        container.scrollLeft + 240,
        container.scrollWidth - container.clientWidth
      )
    } else {
      container.scrollLeft = Math.max(container.scrollLeft - 240, 0)
    }
  }

  useEffect(() => {
    if (containerRef.current) {
      const container = containerRef.current
      const { scrollWidth, scrollLeft, clientWidth } = container

      if (scrollWidth > clientWidth + scrollLeft) {
        setCanScroll((currentState) => ({ ...currentState, right: true }))
      }
    }
  }, [])

  return {
    scrollHandler,
    containerRef,
    buttons: (canScroll.left || canScroll.right) && (
      <ButtonsContainer>
        <Button
          disabled={!canScroll.left}
          onClick={scrollButtonClickHandler.bind(null, false)}
        >
          <ChevronCompactLeft />
        </Button>
        <Button
          disabled={!canScroll.right}
          onClick={scrollButtonClickHandler.bind(null, true)}
        >
          <ChevronCompactRight />
        </Button>
      </ButtonsContainer>
    ),
  }
}

export default useScrollButtons
