import Section from "src/components/common/Section"
import Text from "src/components/common/Text"
import proposalText from "src/constants/text/proposal.json"

const ProposalSection = () => (
  <Section title={proposalText.title} sectionKey="proposal">
    {proposalText.content.map((slice, index) => (
      <Text parse key={index}>
        {slice}
      </Text>
    ))}
  </Section>
)

export default ProposalSection
