import SPACES from "src/constants/styling/spaces"
import { Button, Container } from "./styles"
import indexText from "src/constants/text/index.json"
import FONT_SIZES from "src/constants/styling/font-sizes"
import { createRef, useEffect, useRef, useState } from "react"
import { createArrayWith } from "src/utils/misc"
import { getActiveSection, scrollToActiveButton } from "./utils"

interface SectionButtonsProps {
  small?: boolean
}

const SectionButtons = ({ small }: SectionButtonsProps) => {
  const [activeSection, setActiveSection] = useState(0)
  const containerRef = useRef<HTMLDivElement>(null)
  const [buttonsRefs] = useState(
    createArrayWith(createRef<HTMLButtonElement>, indexText.sections.length)
  )

  const clickHandler = (key: string) => {
    const section = document.querySelector(`#${key}`)

    if (section) {
      let scroll = window.scrollY
      scroll += section.getBoundingClientRect().y
      scroll -= SPACES.large + SPACES.small * 2 + FONT_SIZES.huge

      window.scrollTo({
        top: scroll,
        behavior: "smooth",
      })
    }
  }

  useEffect(() => {
    const scrollHandler = () => {
      const activeSection = getActiveSection()

      setActiveSection((currentValue) => {
        if (currentValue !== activeSection) {
          scrollToActiveButton(buttonsRefs[activeSection], containerRef)
        }

        return activeSection
      })
    }

    window.addEventListener("scroll", scrollHandler)

    return () => {
      window.removeEventListener("scroll", scrollHandler)
    }
  }, [buttonsRefs])

  return (
    <Container small={small} ref={containerRef}>
      {indexText.sections.map((section, index) => (
        <Button
          className={index === activeSection ? "active" : ""}
          small={small}
          key={section.key}
          onClick={clickHandler.bind(null, section.key)}
          ref={buttonsRefs[index]}
        >
          {section.label}
        </Button>
      ))}
    </Container>
  )
}

export default SectionButtons
