import Section from "src/components/common/Section"
import Text from "src/components/common/Text"
import linksText from "src/constants/text/links.json"
import { LinksContainer } from "./styles"
import LinkCard from "./LinkCard"
import useScrollButtons from "src/hooks/useScrollButtons"

const LinksSection = () => {
  const { containerRef, scrollHandler, buttons } =
    useScrollButtons<HTMLUListElement>()

  return (
    <Section title={linksText.title} sectionKey="links">
      {linksText.description.map((slice) => (
        <Text key={slice} parse>
          {slice}
        </Text>
      ))}
      {buttons}
      <LinksContainer ref={containerRef} onScroll={scrollHandler}>
        {linksText.links.map((link) => (
          <LinkCard {...link} />
        ))}
      </LinksContainer>
    </Section>
  )
}

export default LinksSection
